// Define handshake animation keyframes
val HandShakeAnimation = Keyframes {
    0.percent {
        Modifier.rotate(0.deg)
    }
    25.percent {
        Modifier.rotate(20.deg)
    }
    50.percent {
        Modifier.rotate(-10.deg)
    }
    75.percent {
        Modifier.rotate(10.deg)
    }
    100.percent {
        Modifier.rotate(0.deg)
    }
}

// Define floating animation keyframes for up and down movement
val FloatingAnimation = Keyframes {
    0.percent {
        Modifier.translateY(0.px)
    }
    50.percent {
        Modifier.translateY((-15).px)
    }
    100.percent {
        Modifier.translateY(0.px)
    }
}

@InitRoute
fun initHomePage(ctx: InitRouteContext) {
    ctx.data.add(PageLayoutData("Home"))
}

