@Composable
fun AnimatedHandEmoji() {
    var isHovered by remember { mutableStateOf(false) }
    var showAnimation by remember { mutableStateOf(false) }

    // Trigger animation once when component is first composed
    LaunchedEffect(Unit) {
        // Small delay to ensure the page is fully loaded
        delay(500)
        showAnimation = true
        // Reset after animation completes
        delay(500)
        showAnimation = false
    }

    SpanText(
        "",
        modifier = Modifier
            .fontSize(3.cssRem)
            .fontWeight(FontWeight.Bold)
            .cursor(Cursor.Pointer)
            .onMouseEnter { isHovered = true }
            .onMouseLeave { isHovered = false }
            .let {
                if (isHovered || showAnimation) {
                    it.animation(
                        HandShakeAnimation.toAnimation(
                            duration = 500.ms,
                            timingFunction = AnimationTimingFunction.EaseInOut,
                            direction = AnimationDirection.Normal,
                            fillMode = AnimationFillMode.Forwards
                        )
                    )
                } else {
                    it
                }
            }
    )
}

@Composable
fun FloatingPersonImage() {
    var animationTrigger by remember { mutableStateOf(false) }
    var colorMode by ColorMode.currentState
    val personImage = when (colorMode.isLight) {
        true -> "person-light.svg"
        false -> "person-dark.svg"
    }

    // Trigger animation continuously
    LaunchedEffect(Unit) {
        while (true) {
            animationTrigger = true
            delay(3000) // Wait for animation to complete
            animationTrigger = false
            delay(100) // Small delay before starting the next animation
        }
    }

    Box(
        modifier = Modifier
            .let {
                if (animationTrigger) {
                    it.animation(
                        FloatingAnimation.toAnimation(
                            duration = 3000.ms,
                            timingFunction = AnimationTimingFunction.EaseInOut,
                            direction = AnimationDirection.Normal,
                            fillMode = AnimationFillMode.Forwards
                        )
                    )
                } else {
                    it
                }
            },
        contentAlignment = Alignment.Center
    ) {
        Img(
            src = personImage,
            attrs = {
                attr("alt", "Logo")
                style {
                    maxWidth(50.percent)
                    height(Height.FitContent)
                }
            }
        )
    }
}

@Composable
fun ColorModeButton2() {
    var colorMode by ColorMode.currentState
    IconButton(onClick = { colorMode = colorMode.opposite }) {
        if (colorMode.isLight) MoonIcon() else SunIcon()
    }
    Tooltip(ElementTarget.PreviousSibling, "Toggle color mode", placement = PopupPlacement.BottomRight)
}
